import React from 'react'

// material-ui
import Box from '@material-ui/core/Box'
import Toolbar from '@material-ui/core/Toolbar'
import Fab from '@material-ui/core/Fab'
import KeyboardArrowUpIcon from '@material-ui/icons/KeyboardArrowUp'

// custom component
import SiteHeader from '../components/Headers/SiteHeader'
import LandingPage from '../components/LandingPage/LandingPage'
import ScrollTop from '../components/ScrollTop/ScrollTop'
import Footer from '../components/Footer/Footer'

/**
 * Index page
 * @param {Object} props - component properties
 * @param {Object} props.location - representing where the app is from a router perspective
 * @returns {*}
 * @constructor
 */
function Index({ location }) {
  return (
    <React.Fragment>
      <SiteHeader title="Latest news, article, and project" location={location} />
      {/* target for scrollTop component - hidden behind header since header is fixed */}
      <Toolbar id="back-to-top-anchor" />
      <Box pt={8} pr={2} pb={2} pl={2}>
        <LandingPage />
      </Box>
      <ScrollTop>
        <Fab color="secondary" aria-label="scroll back to top">
          <KeyboardArrowUpIcon />
        </Fab>
      </ScrollTop>
      <Footer />
    </React.Fragment>
  )
}

export default Index
