import React from 'react'

// material-ui
import Paper from '@material-ui/core/Paper'
import Grid from '@material-ui/core/Grid'
import Typography from '@material-ui/core/Typography'
import { withStyles } from '@material-ui/core/styles'

// custom components
import Link from '../Links/Link'

// Query latest article
import useLatestNews from '../../data/useLatestNews'

const styles = theme => ({
  root: {
    position: 'relative',
    backgroundColor: theme.palette.grey[800],
    color: theme.palette.common.white,
    marginTop: theme.spacing(1),
    marginBottom: theme.spacing(1),
    backgroundImage: 'url(https://source.unsplash.com/user/erondu)',
    backgroundSize: 'cover',
    backgroundRepeat: 'no-repeat',
    backgroundPosition: 'center'
  },
  overlay: {
    position: 'absolute',
    top: 0,
    bottom: 0,
    right: 0,
    left: 0,
    backgroundColor: 'rgba(0,0,0,.3)'
  },
  content: {
    position: 'relative',
    padding: theme.spacing(3),
    [theme.breakpoints.up('md')]: {
      padding: theme.spacing(6),
      paddingRight: 0
    }
  }
})

/**
 * Render the latest article / blog
 * @param {Object} props - component properties
 * @param {Object} props.classes - override the injected class names
 * @returns {*}
 * @constructor
 */
function LatestNews({ classes }) {
  const { node: { excerpt, timeToRead, fields: { slug }, frontmatter: { title, date } } } = useLatestNews()

  return (
    <Paper className={classes.root}>
      {<img
        style={{ display: 'none' }}
        src="https://source.unsplash.com/daily?news"
        alt="latest article background"
      />}
      <div className={classes.overlay} />
      <Grid container>
        <Grid item md={6}>
          <div className={classes.content}>
            <Typography component="h1" variant="h3" color="inherit" gutterBottom>
              {title}
            </Typography>
            <Typography component='div' variant="caption" color="inherit">
              {date}
            </Typography>
            <Typography component='div' variant="caption" color="inherit" gutterBottom>
              {timeToRead + " minutes"}
            </Typography>
            <Typography variant="h5" color="inherit" paragraph>
              {excerpt}
            </Typography>
            <Link variant="subtitle1" color='inherit' to={slug}>
              Continue reading â€¦
            </Link>
          </div>
        </Grid>
      </Grid>
    </Paper>
  )
}

export default withStyles(styles)(LatestNews)
