import { useStaticQuery, graphql } from 'gatsby'

/**
 * Query tags by article an return them as a flat array
 * Shit in a label for all tags as well to be used to link t the all tags page
 */
export default function useTagsByArticles() {
  const { allMarkdownRemark } = useStaticQuery(graphql`
    query allTagsByArticles {
      allMarkdownRemark(
          filter: { 
              fileAbsolutePath: { regex: "/(articles)/.*\\\\.md$/" }
              frontmatter: { published: { ne: "No" } }
          }
      ) {
        group(field: frontmatter___tags) {
          tag: fieldValue
        }
      }
    }
  `)

  /**
   * Return a flat array of tags
   */
  const allTags = allMarkdownRemark.group.map(tag => tag.tag)
  allTags.unshift('All')
  return allTags
}
