import React from 'react'

// material-ui
import Paper from '@material-ui/core/Paper'
import Box from '@material-ui/core/Box'
import Typography from '@material-ui/core/Typography'

// custom component
import Link from '../Links/Link'

const archives = [
  'March 2020',
  'February 2020',
  'January 2020',
  'December 2019',
  'November 2019',
  'October 2019',
  'September 2019',
  'August 2019',
  'July 2019',
  'June 2019',
  'May 2019',
  'April 2019'
]

function ArticlesArchive() {
  return (
    <Box clone p={1}>
      <Paper elevation={1}>
        <Typography variant="h6" gutterBottom>
          Article archives
        </Typography>
        {archives.map(archive => (
          <Link display="block" variant="body1" to="/" key={archive}>
            {archive}
          </Link>
        ))}
      </Paper>
    </Box>
  )
}

export default ArticlesArchive
