import React from 'react'

// material-ui
import Container from '@material-ui/core/Container'
import Box from '@material-ui/core/Box'
import Grid from '@material-ui/core/Grid'
import Divider from '@material-ui/core/Divider'

// custom components
import ArticlesTagsToolbar from './ArticlesTagsToolbar'
import LatestNews from './LatestNews'
import LatestArticle from './LatestArticle'
import LatestProject from './LatestProject'
import ArticlesArchive from './ArticlesArchive'
import SocialMedia from './SocielMedia'

/**
 * Landing page for the site
 * @param {Object} props - component properties
 * @param {Object} props.classes - override the injected class names
 * @returns {*}
 * @constructor
 */
function LandingPage({ classes }) {
  return (
    <Container maxWidth="lg">
      <ArticlesTagsToolbar />
      <Box pt={1} pb={1}>
        <Divider variant="fullWidth" />
      </Box>
      <LatestNews />
      <Box pt={1} pb={1}>
        <Divider variant="fullWidth" />
      </Box>
      <Grid container spacing={2}>
        {/* Main content */}
        <Grid item xs={12} md={8}>
          <LatestArticle />
          <Box pt={1} pb={1}>
            <LatestProject />
          </Box>
        </Grid>
        {/* End main content */}
        {/* Sidebar */}
        <Grid item xs={12} md={4}>
          <ArticlesArchive />
          <Box pt={1} pb={1}>
            <SocialMedia />
          </Box>
        </Grid>
        {/* End sidebar */}
      </Grid>
    </Container>
  )
}

export default LandingPage
